
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  min-width: 1300px;
  color: #2c3e50;
}

@font-face {
  font-family: 'pingfang';
  src: url(/static/fonts/pingfang.24712f6.ttf);
}

body,html {
  margin: 0;
  padding: 0;
  font-family: 'pingfang, Avenir, Helvetica, Arial, sans-serif';
  min-height: 100%;
  background: #f6f8fa;
}

.wrapper {
  min-height: 700px;
  background-color: #f6f8fa;
  margin-bottom: 40px;
}

.header {
  height: 70px !important;
  margin-bottom: 20px;
  background: #fff;
  -webkit-box-shadow: 0 2px 4px rgba(0, 0, 0, .12);
          box-shadow: 0 2px 4px rgba(0, 0, 0, .12)
}

.left-bar-title {
  margin: 0 ;
  padding: 20px 0;
  text-align: center;
}

.left-menu {
  z-index: 10;
  border: 1px solid #f2f2f3;
  background: #fff;
  -webkit-box-shadow: 0 0 30px 0 rgba(145,159,165,.1);
          box-shadow: 0 0 30px 0 rgba(145,159,165,.1);
  overflow: hidden
}

.right-content {
  background: #fff;
}

.child-item {
  padding-left: 50px !important;
}

.home .el-breadcrumb {
  height: 35px;
  line-height: 35px;
  margin-bottom: 15px;
  /* border-bottom: 1px solid #ddd; */
}

.tabs-icon {
  margin-right: 4px;
}

/* element-ui */

.el-menu {
  /* background: transparent !important;
  border-right: 0 !important; */
}

.el-main {
  padding: 10px 20px !important;
  background-color: #fff;
  -webkit-box-shadow: 0 0 30px 0 rgba(145,159,165,.1);
          box-shadow: 0 0 30px 0 rgba(145,159,165,.1);
  margin: 0 20px;
}
